    #t_3_1.titulo-segundo.mb-4
      h2 3.1  Proporcin vs. canon

    p.mb-5 Esta comparacin hace relacin sobre cuntas cabezas caben en la altura del cuerpo.

    TabsA.color-acento-contenido.mb-5
      //- .tarjeta debe ir acompaado de una de una de estas clases =>
