        p.mb-5 De acuerdo con lo anterior, es importante saber cmo obtener medidas deducidas. Aqu un ejemplo: para deducir la medida de 7 cervical, se toma la medida total de una persona y se le resta 20 cm. Por qu 20 cm? Porque en la estandarizacin de medidas, se establece que 20 cm corresponde a la medida aprox. de la cabeza. A continuacin se presenta la frmula:
