      .row(titulo="Errores ms comunes")
        p.mb-4 Uno de los ms comunes en modistera es la combinacin de medidas. Cuando se trabaja con medidas deducidas, se debe realizar con las medidas obtenidas de cada frmula de manera progresiva; para eso hay que tener en cuenta que el resultado cambia si, en vez de aplicar nicamente las medidas obtenidas por deduccin, se debe adicionar una medida anatmica.
          br
          | De acuerdo con lo anterior, es importante establecer los cuadros de tallas:
        .row.justify-content-center
          .col-11
            ul.lista-ul--color
              li.mb-0
                i.fas.fa-check
                | <strong>Anatmicos y deducidos</strong>: segn la obtencin de medidas.
              li.mb-0
                i.fas.fa-check
                | <strong>Proporcionales</strong>: existe una secuencia numrica en el crecimiento de la talla.
              li.mb-0
                i.fas.fa-check
                | <strong>No proporcionales</strong>: no hay una secuencia numrica en el crecimiento de la talla.
              li.mb-0
                i.fas.fa-check
                | <strong>Completos</strong>: incluyen todas las medidas requeridas para la construccin de patrones de prendas.
              li.mb-0
                i.fas.fa-check
                | <strong>Segn prenda</strong>: que incluyen en algunos casos desahogos y costuras.


      .row(titulo="Normatividad")
        p.mb-4 Por lo general no son de uso obligatorio, sino de uso voluntario; y dentro de la normatividad nacional se puede encontrar:
        .row.justify-content-center
          .col-11
            ul.lista-ul--color
              li.mb-0
                i.fas.fa-check
                p.mb-0 <strong>NTC ISO 8559</strong>. Fabricacin de vestuario y estudios antropomtricos. Dimensiones corporales  Icontec.
              li.mb-0
                i.fas.fa-check
                p.mb-0 <strong>Norma UNE-EN 13402-3</strong>. Armoniza y simplifica el etiquetado de tallas de prendas de vestir para el beneficio del consumidor y de la industria en general.

      .row(titulo="Anlisis de cuadros de tallas")
        p.mb-4 Para el anlisis de cuadros de tallas se deben determinar los lmites, la frecuencia y el incremento, tal como se relaciona a continuacin:
        .row.justify-content-center
          .col-11
            ul.lista-ul--color
