    #t_3_3.titulo-segundo.mb-4
      h2 3.3  Proporciones en la deduccin de medidas

    p.mb-4 El aumento secuencial estandarizado para el incremento o disminucin de tallas se encuentra identificando la diferencia en unidades de medida de talla a talla en cada lnea de medida del cuerpo. En este sentido, la frmula estandarizada para hallar la proporcin es: diferencia del contorno mayor dividido en 4, de all resultan las fracciones de la proporcin, como se relaciona en el siguiente ejemplo:

    .row.justify-content-center.mb-5
      .col-11
        ul.lista-ul--color
          li
            i.fas.fa-check
            | 1 proporcin ser 1 cm
          li
            i.fas.fa-check
            |  proporcin ser 0,5 cm
          li
            i.fas.fa-check
            |  proporcin ser 0,25 cm y 1.5 proporcin ser 1,5 cm

    .row
      .col-6
        .BorTit.mb-4 Figura 24 Proporciones en la deduccin
    .row.mb-4
      .col
        img(src='@/assets/curso/tema3-figure24.png')
      .col
        p Para realizar la deduccin de medidas es importante realizar su comparacin y para ello se debe tener en cuenta:
        .row.justify-content-center
          .col-11
            ul.lista-ul--color
              li
                i.fas.fa-check
                | Incremento entre talla y talla: tomar medida por medida y revisar el incremento que hay entre talla y talla.
              li
                i.fas.fa-check
                | Ruptura de talla: identificar ruptura del cuadro de tallas, por medida y talla.
        .BorTit.mb-4 Figura 25 Comparacin de medidas
        img(src='@/assets/curso/tema3-figure25.png')

    p.mb-5 En la figura anterior, la ruptura est entre la talla 14 y 16, porque se incrementa 6 cm, rompiendo con los 4 cm de las dems medidas.

    #t_3_4.titulo-segundo.mb-4
      h2 3.4  Identificar el DROP

    p DROP es la palabra tcnica que indica la diferencia en centmetros entre dos medidas del cuerpo que permite determinar su morfologa, tal como se puede observar aqu:

    .row.mb-4
      .col
        .row.justify-content-center
          .col-11
            ul.lista-ul--color
              li
                i.fas.fa-check
                p.mb-0 <strong>DROP femenino</strong>: diferencia entre cadera y contorno de busto para la mujer. Es decir, <strong>Contorno de Cadera - Contorno de Busto = DROP</strong>
        p.mb-4 El DROP tiene caractersticas importantes que se deben considerar al momento de realizar su identificacin:
        .row.justify-content-center.mb-4
          .col-11
            ul.lista-ul--color.mb-0
              li.mb-0
                i.fas.fa-check
                | Con el DROP se establecen las diferentes contexturas de una misma talla.
              li.mb-0
                i.fas.fa-check
                | En las mujeres, el DROP es la diferencia entre las medidas de Contorno de Cadera y Contorno de Busto.
              li.mb-0
                i.fas.fa-check
                | En las tallas femeninas predomina un DROP positivo.
              li.mb-0
                i.fas.fa-check
                | Generalmente una talla se considera normal cuando el DROP es positivo entre +2 cm y +4 cm.
              li.mb-0
                i.fas.fa-check
                | El DROP estndar usado en Colombia es + 4 cm.
              li.mb-0
                i.fas.fa-check
                | En las tallas extremas y altas el DROP se vuelve negativo.
        p.mb-4 A continuacin, se relacionan algunos ejemplos:
        .row.justify-content-center
          .col-11
            ul.lista-ul--color
              li.mb-0
                i.fas.fa-check
                p.mb-0 <strong>DROP + 2</strong>, indica la medida de cadera 2 cm mayor que el contorno de busto.
              li.mb-0
                i.fas.fa-check
                p.mb-0 <strong>El DROP 0</strong>, indica que la medida de cadera es igual a la medida de contorno de busto.
              li.mb-0
                i.fas.fa-check
                p.mb-0 <strong>El DROP - 4</strong>, indica que la medida de cadera tiene 4 cm menos que el contorno de busto.

      .col
        .px-5.py-3.mb-4(style='background-color: #F7F1D7')
          .BorTit.mb-4 Figura 26 DROP femenino
          img(src='@/assets/curso/tema3-figure26.svg', style='max-height: 400px')

    .row.mb-5
      .col
        .BorTit.mb-4 Figura 27 DROP masculino
        img(src='@/assets/curso/tema3-figure27.svg')
      .col
        .row.justify-content-center.mb-4
          .col-11
            ul.lista-ul--color
              li.mb-0
                i.fas.fa-check
                p.mb-0 <strong>DROP masculino</strong>: para este caso la diferencia est entre la cintura y el pecho. Generalmente en tallas masculinas predomina el DROP negativo (ms pecho que cintura), aunque al realizar una tabla de tallas (a mayor talla menos DROP) en tallas extremas puede a ser positivo, es decir, un cuerpo con ms cintura que pecho.
        p.mb-4 A continuacin, se relacionan algunos ejemplos:
        .row.justify-content-center
          .col-11
            ul.lista-ul--color
              li.mb-0
                i.fas.fa-check
                p.mb-0 <strong>Un DROP - 4</strong> indica, una cintura con 4 pulgadas menos que la medida de pecho.
              li.mb-0
                i.fas.fa-check
                p.mb-0 <strong>Un DROP 0</strong>, indica una cintura igual al pecho.
              li.mb-0
                i.fas.fa-check
                p.mb-0 <strong>Un DROP + 2</strong>, indica una cintura con 2 pulgadas que el pecho.

    p.mb-5 Acorde con el gnero, cada tipo de silueta tiene un DROP caracterstico determinado, a su vez, por la tipologa del cuerpo dentro de una clasificacin general, puesto que hay particularidades y cada persona necesita un anlisis en cuanto a su silueta para poder abordar el vestuario, patronaje y confeccin.

    .BorTit.mb-4 Figura 28 Tipos de siluetas generales masculinas y femeninas

    img(src='@/assets/curso/tema3-figure28.svg')


